body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html {
  height: 100%;
  background: url(/avalon/static/media/parchment.bc061578.jpg) center no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  font-family: 'Great Vibes', 'Stalemate', cursive; }

.warning {
  color: #990000;
  font-weight: bold; }

.btn {
  font-family: 'MedievalSharp', cursive;
  font-size: 16px;
  font-weight: 600;
  height: 36px;
  border-radius: 22px; }
  .btn.common {
    background-color: #804000;
    border: 2px solid white;
    color: white; }
  .btn.clear {
    background-color: transparent;
    border: 2px solid black;
    color: black; }
  .btn:hover {
    cursor: pointer; }
  .btn:disabled {
    opacity: 0.5;
    cursor: default; }

input {
  width: 130px;
  height: 36px;
  padding: 0 15px;
  border-radius: 22px;
  border: none;
  font-size: 16px; }

.player-setup {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .player-setup .title {
    margin-bottom: 10px;
    font-size: 30px; }
  .player-setup .name-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding-left: 50px; }
    .player-setup .name-section .name-item {
      margin-top: 5px; }
      .player-setup .name-section .name-item:first-child {
        margin-top: 10px; }
  .player-setup .btn {
    padding: 0 16px; }
    .player-setup .btn.add-player {
      margin-left: 10px; }
    .player-setup .btn.start-game {
      margin-top: 10px; }
  .player-setup .warning {
    margin: 10px 0; }

